<template>
  <v-card>
    <v-card-title>Upgrade to Latest</v-card-title>
    <v-card-text>
      <v-row>
        <v-col cols="12">
          <v-autocomplete
            v-model="selectedSlaveApps"
            label="Select Slave Apps"
            return-object
            item-text="device_name"
            item-value="slaveId"
            class="text-left"
            :items="slaveApps"
            chips
            clearable
            deletable-chips
            multiple
            small-chips
          ></v-autocomplete>
        </v-col>
      </v-row>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        x-small
        class="mr-1 btn-height-22 white--text"
        color="blue darken-1"
        :disabled="selectedSlaveApps.length == 0"
        :loading="isLoading"
        @click="() => upgradeToLatest()"
        >Upgrade to latest</v-btn
      >
      <v-btn x-small class="mr-1 btn-height-22 white--text" @click="onClose"> Cancel </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
export default {
  name: 'upgrade-to-latest',
  props: ['slaveApps', 'onClose', 'selectedGridItems', 'upgradeLatest'],
  data() {
    return {
      isLoading: false,
      selectedSlaveApps: [],
    };
  },
  created() {
    if (this.selectedGridItems.length > 0) {
      this.selectedSlaveApps = [...this.selectedGridItems];
    }
  },
  methods: {
    upgradeToLatest() {
      this.isLoading = true;
      return this.upgradeLatest(this.selectedSlaveApps)
        .then(data => {
          const isEverySuccess = data.every(item => item.success);
          if (isEverySuccess) {
            this.$toasted.show('Successfully updated selected apps');
            this.onClose();
          }
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
};
</script>
