import http from '../axios';

const endpoint = '/slave-devices';

export const create = slave => {
  const body = {
    global_uuid: slave.global_uuid,
  };
  return http.post(endpoint, body);
};

export const bulkCreate = slaves => {
  return Promise.all(slaves.map(slave => create(slave)));
};

export const fetch = () => {
  return http.get(endpoint);
};

export const discoverDevices = () => {
  return http.get(`${endpoint}/discover`);
};

export const remove = slave => {
  const globalUUID = slave.global_uuid;
  return http.delete(`${endpoint}/${globalUUID}`);
};

export const bulkRemove = slaves => {
  return Promise.all(slaves.map(slave => remove(slave)));
};

export const updateComment = (uuid, comment) => {
  return http.put(`${endpoint}/${uuid}/comment`, comment);
};

export const removeComment = uuid => {
  return http.delete(`${endpoint}/${uuid}/comment`);
};

export const updateTags = (uuid, tags) => {
  return http.put(`${endpoint}/${uuid}/tags`, tags);
};

export const removeTags = uuid => {
  return http.delete(`${endpoint}/${uuid}/tags`);
};

export const syncSlaveDevices = () => {
  return http.get(`${endpoint}/sync`);
};
