<template>
  <div>
    <v-card :loading="isFetchingApps">
      <v-toolbar flat>
        <v-toolbar-title>
          <span>BIOS Apps</span>
          <tooltip-icon-button
            btnColor="default"
            btnIcon="mdi-sync"
            btnTooltip="Refresh BIOS apps detail"
            buttonSize="medium"
            :onClick="() => initialize()"
          >
          </tooltip-icon-button>
        </v-toolbar-title>
        <v-text-field
          v-model="search"
          @input="onSearchChange"
          class="pl-10"
          append-icon="mdi-magnify"
          label="Search using site or device name"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-card-text>
        <v-container fluid>
          <v-alert
            v-if="!isTokenValid"
            class="text-left darken-1"
            border="right"
            dismissible
            dense
            type="info"
            elevation="2"
          >
            <span class="pr-5">{{ errMessage }}</span>
            <v-btn
              @click="() => updateAllToken()"
              :disabled="items.length == 0"
              x-small
              color="nubeGreen"
              class="btn-height-22 white--text mr-3"
            >
              Update Token
              <v-icon small right dark> mdi-key-outline </v-icon>
            </v-btn>
          </v-alert>
          <v-row no-gutter>
            <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
              <v-card outlined>
                <v-toolbar dense flat>
                  <v-toolbar-title class="pr-3">
                    <span>Rubix Services</span>
                  </v-toolbar-title>
                  <span class="text-left mr-5">
                    <span class="grey-text font-weight-medium caption">Selected Apps: </span>
                    <span class="font-weight-medium">{{ selectedGridItems.length }}</span>
                  </span>
                  <v-btn
                    @click="() => toggleDialog(dialogNames.upgradeToLatest)"
                    :disabled="items.length == 0"
                    x-small
                    color="blue"
                    class="btn-height-22 white--text mr-3"
                  >
                    Upgrade to Latest
                    <v-icon x-small right dark> mdi-arrow-right </v-icon>
                  </v-btn>
                  <v-btn
                    @click="() => toggleDialog(dialogNames.updateToken)"
                    :disabled="items.length == 0"
                    x-small
                    color="nubeGreen"
                    class="btn-height-22 white--text mr-3"
                  >
                    Update Token
                    <v-icon x-small right dark> mdi-key-outline </v-icon>
                  </v-btn>
                  <v-btn
                    @click="() => toggleDialog(dialogNames.restart)"
                    :disabled="items.length == 0"
                    x-small
                    color="pink"
                    class="btn-height-22 white--text mr-3"
                  >
                    Restart
                    <v-icon x-small right dark> mdi-restart </v-icon>
                  </v-btn>
                  <v-spacer></v-spacer>
                  <span v-if="latestVersion">
                    <span class="grey-text font-weight-medium caption pr-2">Latest Version</span>
                    {{ latestVersion }}
                  </span>
                </v-toolbar>
                <v-data-table
                  :fixed-header="isFixedHeader"
                  hide-default-footer
                  :items="dataItems"
                  :single-select="false"
                  show-select
                  @pagination="getPageLength"
                  :height="dataTableHeight"
                  :items-per-page="10000"
                  :custom-filter="filterCriteria"
                  :expanded.sync="expanded"
                  single-expand
                  item-key="slaveId"
                  :search="search"
                  :headers="headers"
                  v-model="selectedGridItems"
                  show-expand
                >
                  <template v-slot:[`item.site_name`]="{ item }">
                    <span>
                      <master-star-indicator :is-master="item.is_master"></master-star-indicator>
                      <span>{{ item.site_name }}</span>
                    </span>
                  </template>
                  <template v-slot:[`item.upgrade_app_state`]="{ item }">
                    <app-status-chip chipSize="small" :state="item.upgrade_app_state"></app-status-chip>
                  </template>
                  <template v-slot:expanded-item="{ headers, item }">
                    <td
                      :colspan="headers.length"
                      :class="`expanded-table-row py-2 ${$vuetify.theme.dark ? 'nubeDarkGrey' : 'nubeLightBackground'}`"
                    >
                      <div class="flex pl-15">
                        <span class="table-expanded-item">
                          <span class="grey-text font-weight-medium caption">Timezone</span>
                          <span class="text-ellipses">{{ item.time_zone }}</span>
                        </span>
                        <span class="table-expanded-item">
                          <span class="grey-text font-weight-medium caption">State</span>
                          <span class="font-weight-medium text-ellipses">{{ item.site_state }}</span>
                        </span>
                        <span class="table-expanded-item">
                          <span class="grey-text font-weight-medium caption">City</span>
                          <span class="font-weight-medium text-ellipses">{{ item.site_city }}</span>
                        </span>
                        <span class="table-expanded-item">
                          <span class="grey-text font-weight-medium caption">Zip</span>
                          <span class="font-weight-medium text-ellipses">{{ item.site_zip }}</span>
                        </span>
                      </div>
                    </td>
                  </template>
                </v-data-table>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>
    <v-dialog v-model="dialog[dialogNames.upgradeToLatest]" persistent max-width="500px">
      <upgrade-latest-slave-bios-apps
        v-if="dialog[dialogNames.upgradeToLatest]"
        :slave-apps="dataItems"
        :upgrade-latest="upgradeLatest"
        :selected-grid-items="selectedGridItems"
        :on-close="() => closeDialog(dialogNames.upgradeToLatest)"
      ></upgrade-latest-slave-bios-apps>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.updateToken]" persistent max-width="500px">
      <slaves-update-token
        v-if="dialog[dialogNames.updateToken]"
        :slave-apps="dataItems"
        :update-token="updateToken"
        :selected-grid-items="selectedGridItems"
        :on-close="() => closeDialog(dialogNames.updateToken)"
      ></slaves-update-token>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.restart]" persistent max-width="500px">
      <restart-bios-slave-apps
        v-if="dialog[dialogNames.restart]"
        :slave-apps="dataItems"
        :restart-apps="restartApps"
        :selected-grid-items="selectedGridItems"
        :on-close="() => closeDialog(dialogNames.restart)"
      ></restart-bios-slave-apps>
    </v-dialog>
  </div>
</template>
<script>
const MAX_VISIBLE_GRID_ITEMS = 20;
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import * as slaveBiosAppsService from '../../services/slavesBiosAppsService';
import * as slaveDevicesService from '../../services/slaveDevicesService';
import * as rubixBiosService from '../../services/rubixBiosService';
import MasterStarIndicator from '../Common/MasterStarIndicator.vue';
import UpgradeLatestSlaveBiosApps from './upgradeLatestSlaveBiosApps.vue';
import SlavesUpdateToken from './slavesUpdateToken.vue';
import RestartBiosSlaveApps from './restartBiosSlaveApps.vue';
import AppStatusChip from '../Services/AppStatusChip.vue';
const dialogNames = {
  upgradeToLatest: 'upgradeToLatest',
  restart: 'restart',
  updateToken: 'updateToken',
};
export default {
  name: 'slave-bios-apps',
  components: {
    TooltipIconButton,
    UpgradeLatestSlaveBiosApps,
    SlavesUpdateToken,
    MasterStarIndicator,
    RestartBiosSlaveApps,
    AppStatusChip,
  },
  data() {
    return {
      search: '',
      errMessage: '',
      isTokenValid: true,
      isFetchingLatestVersion: false,
      expanded: [],
      isFixedHeader: false,
      isFetchingApps: false,
      dataTableHeight: undefined,
      items: [],
      selectedGridItems: [],
      latestVersion: '',
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.upgradeToLatest]: false,
        [dialogNames.restart]: false,
        [dialogNames.updateToken]: false,
      },
      headers: [
        {
          text: 'S/N',
          align: 'center',
          value: 'sn',
          sortable: true,
        },
        {
          text: 'Site Name',
          align: 'left',
          value: 'site_name',
        },
        {
          text: 'Device Name',
          align: 'left',
          value: 'device_name',
        },
        {
          text: 'Upgrade Status',
          align: 'center',
          value: 'upgrade_app_state',
        },
        {
          text: 'Installed Version',
          align: 'center',
          sortable: true,
          value: 'version',
        },
        {
          text: 'Token',
          align: 'center',
          sortable: true,
          value: 'token',
        },
        {
          text: 'Active Since',
          align: 'center',
          sortable: true,
          value: 'time_since',
        },
        { text: '', value: 'data-table-expand' },
      ],
    };
  },
  computed: {
    selectedServicesCount() {
      return 0;
    },
    dataItems() {
      return this.items.map((item, index) => ({
        ...item,
        sn: index + 1,
        site_name: item.is_master ? `   ${item.site_name}` : item.site_name,
      }));
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.fetchLatestVersion();
      this.fetchSlaveAppsDevices();
    },
    fetchLatestVersion() {
      this.isFetchingLatestVersion = true;
      return rubixBiosService
        .fetchLatestVersion()
        .then(data => {
          let latestVersion = '';
          if (data) {
            if (Array.isArray(data) && data.length > 0) {
              latestVersion = data[0];
            } else if (data.version) {
              latestVersion = data.version;
            }
          }
          this.latestVersion = latestVersion;
          this.isTokenValid = true;
        })
        .catch(err => {
          err && err.message && (this.errMessage = err.message);
          this.isTokenValid = false;
        });
    },
    fetchSlaveAppsDevices() {
      this.isFetchingApps = true;
      Promise.all([this.fetchApps(), this.fetchSlaveDevices()])
        .then(([slaveApps, slaveDevices]) => {
          this.items = slaveBiosAppsService.mergeSlaveBiosAppDetails(slaveApps, slaveDevices);
        })
        .catch(err => {
          // show retry option
        })
        .finally(() => {
          this.isFetchingApps = false;
          this.selectedGridItems = [];
        });
    },
    restartApps(slaveApps) {
      const slaveIds = slaveApps.map(slaveApp => slaveApp.slaveId);
      return slaveBiosAppsService.bulkRestartBios(slaveIds).finally(()=> {
        this.$toasted.show(
          'Rubix services are been restarted. It may be unresponsive for a few seconds in all locations.',
          { type: 'info' },
        );
      });
    },
    upgradeLatest(slaveApps) {
      const slaveIds = slaveApps
        .filter(slaveApp => slaveApp.version !== this.latestVersion)
        .map(slaveApp => slaveApp.slaveId);

      return slaveBiosAppsService.bulkUpdateAll(slaveIds).then(data => {
        return data;
      });
    },
    updateToken(slaveApps, token) {
      const slaveIds = slaveApps.map(slaveApp => slaveApp.slaveId);
      return slaveBiosAppsService.bulkUpdateToken(slaveIds, { token }).then(data => {
        this.fetchSlaveAppsDevices();
        this.fetchLatestVersion();
        return data;
      });
    },
    updateAllToken() {
      this.selectedGridItems = [...this.items];
      this.toggleDialog(dialogNames.updateToken);
    },
    fetchApps() {
      return slaveBiosAppsService.fetchApps();
    },
    fetchSlaveDevices() {
      return slaveDevicesService.fetch();
    },
    toggleDialog(dialogName) {
      this.dialog[dialogName] = !this.dialog[dialogName];
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
    },
    getPageLength(pagination) {
      if (pagination.itemsLength < MAX_VISIBLE_GRID_ITEMS) {
        this.dataTableHeight = undefined;
      } else {
        this.dataTableHeight = '80vh';
      }
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
