<template>
  <v-card>
    <v-card-title>Update Token</v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-autocomplete
              v-model="selectedSlaveApps"
              label="Select Slave Apps"
              return-object
              item-text="device_name"
              item-value="slaveId"
              class="text-left"
              :items="slaveApps"
              chips
              clearable
              deletable-chips
              multiple
              small-chips
            ></v-autocomplete>
          </v-col>
          <v-col cols="12">
            <v-text-field label="Third party token" v-model="token" :rules="tokenRules"></v-text-field>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        x-small
        class="mr-1 btn-height-22 white--text"
        color="blue darken-1"
        :disabled="selectedSlaveApps.length == 0"
        :loading="isLoading"
        @click="() => onSave()">
        Update Token
      </v-btn>
      <v-btn x-small class="mr-1 btn-height-22 white--text" @click="onClose"> Cancel </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import validation from '../../common/validation';

export default {
  name: 'upgrade-to-latest',
  props: ['slaveApps', 'onClose', 'selectedGridItems', 'updateToken'],
  data() {
    return {
      valid: false,
      isLoading: false,
      selectedSlaveApps: [],
      token: '',
      tokenRules: [validation.required],
    };
  },
  created() {
    if (this.selectedGridItems.length > 0) {
      this.selectedSlaveApps = [...this.selectedGridItems];
    }
  },
  methods: {
    onSave() {
      if (!this.$refs.form.validate()) return;
      this.isLoading = true;
      return this.updateToken(this.selectedSlaveApps, this.token)
        .then(data => {
          const isEverySuccess = data.every(item => item.success);
          if (isEverySuccess) {
            this.$toasted.show('Successfully updated token selected apps');
            this.onClose();
          }
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
};
</script>
